/* Loading */

.pokeball {
	width: 60px;
	height: 60px;
	background-color: #fff;
	border-radius: 50%;
	position: relative;
	overflow: hidden;
	border: 4px solid;
	animation: frames 0.7s linear 0s infinite;
}

.pokeball:after {
	content: "";
	position: absolute;
	width: 60px;
	height: 32px;
	background-color: red;
	border-bottom: 4px solid;
	top: -4px;
}

.pokeball:before {
	content: "";
	position: absolute;
	background-color: #fff;
	width: 16px;
	height: 16px;
	border: 4px solid;
	border-radius: 50%;
	bottom: 18px;
	right: 18px;
	z-index: 1;
}

/* AnimationFrames */
@keyframes frames {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
